import axios from 'axios'
import React, { useState } from 'react'
import Spinner from '../Spinner/Spinner'
import { useQuery } from 'react-query'
import { Helmet } from 'react-helmet'



export default function Brands() {
  function getAllBrands() {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/brands`)
  }
  let [path, setPath] = useState()
  let { isLoading, data } = useQuery(["allBrands"], getAllBrands)
  function model(event) {
    setPath(event.target.getAttribute('src'))
  }
  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Brands</title>
      </Helmet>
      {isLoading ? <Spinner /> :


        <div className="container my-5">
          <h1 className='text-center text-main fw-bold mb-5'>All Brands</h1>
          <div className="row g-4">
            {data?.data?.data.map((element) => {
              return <div key={element._id} className="col-lg-3 col-md-6 position-relative" data-bs-toggle="modal" data-bs-target="#staticBackdrop" onClick={(event) => { model(event) }}>
                <div className='category cursor-pointer'>
                  <img src={element.image} className='w-100' alt="" />
                  <h6 className='text-center'>{element.name}</h6>
                </div>
              </div>
            })}


            <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
              <div class="modal-dialog">
                <div class="modal-content">
                  <div class="modal-header">
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                  </div>
                  <div class="modal-body">
                    <img src={path} className='w-100 border border-1 rounded-1' />
                  </div>
                  <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                  </div>
                </div>
              </div>
            </div>

          </div>
        </div>}


    </>
  )
}
