import React, { useContext, useEffect, useState } from 'react'
import { CartContext } from '../../Context/cartContext'
import Spinner from '../Spinner/Spinner'
import { Link } from 'react-router-dom'
import { Helmet } from 'react-helmet'
export default function Cart() {
  let { getUserCart, clearCart, removeCartItem, setNumItem, updateCart } = useContext(CartContext)
  let [cartData, setCartData] = useState(null)
  let [Loading, setLoading] = useState(true)
  useEffect(() => {
    getUserData()
  }, [])
  async function getUserData() {
    setLoading(true)
    let request = await getUserCart().catch((error) => {
      if (error.response.data.statusMsg == 'fail') {
        setCartData(null)
        setLoading(false)
      }
    })
    if (request?.data?.status == "success") {
      setLoading(false)
      setCartData(request.data.data)
    }
  }
  async function removeItem(id) {
    let request = await removeCartItem(id)
    if (request?.data.status == "success") {
      setNumItem(request.data.numOfCartItems)
      setCartData(request.data.data)
    }
  }
  async function clearDataCart() {
    let request = await clearCart()
    if (request.data.message == 'success') {
      setCartData(null)
    }
  }
  async function updateDataCart(id, count) {
    if(count == 0){
      removeItem(id)
    }else{
      let request = await updateCart(id, count)
      if (request?.data.status == "success") {
        setCartData(request.data.data)
      }
    }
    
  }
  return (
    <>
    <Helmet>
        <meta charSet="utf-8" />
        <title>Cart</title>
      </Helmet>
      {Loading ? <Spinner /> : <>
        {cartData == null ? <div className='container bg-main-light p-5 mt-5'>
          <h1>Your cart is empty</h1>
        </div> : <div  className='container bg-main-light pb-3 mt-5'>

          <div  className="row">
            <div className='d-flex justify-content-between mt-5'>
              <div className='p-5'>
                <h2 className='mb-4 fw-bold'>Cart Shop</h2>
                <h3>total price: <span className='text-main'> {cartData.totalCartPrice} EGP</span></h3>
              </div>
              <div className='p-5'>
                <Link to={'/checkout/' + cartData._id} className='btn btn-primary d-block ms-auto mb-4'>Check Out</Link>
                <h3>total number of items: <span className='text-main'> {cartData.numOfCartItems} num </span></h3>
              </div>
            </div>
            {cartData.products.map((element) => {
              return <>
                <div key={element.id} className="col-md-10">

                  <div className="row">
                    <div className="col-md-2 mb-2">
                      <img src={element.product.imageCover} className='w-100' alt="" />
                    </div>
                    <div className="col-md-10 d-flex align-items-center">
                      <div>
                        <p className='fw-bold'> {element.product.title} </p>
                        <span>{element.price} EGP</span>
                        <button onClick={() => removeItem(element.product._id)} className='cursor-pointer d-block mt-4 btn btn-outline-danger'><i className="fa-solid fa-trash"></i> Remove</button>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-md-2 ">
                  <div className='mt-5'>
                    <span onClick={() => updateDataCart(element.product._id, element.count += 1)} className='cartIcon cursor-pointer'><i className="fa-solid  fa-plus"></i></span>
                    <span className='mx-3 fw-bold'>{element.count}</span>
                    <span onClick={() => updateDataCart(element.product._id, element.count -= 1)} className='cartIcon cursor-pointer'><i className="fa-solid  fa-minus"></i></span>
                  </div>
                </div>
                <hr></hr>
              </>
            })}

          </div>
          <button onClick={clearDataCart} className='text-main button-cart p-2 d-block mx-auto rounded-2'>Clear Your Cart</button>

        </div>}
      </>}


    </>
  )
}
