import { useFormik } from 'formik';
import React, { useContext } from 'react'
import { useParams } from 'react-router-dom'
import * as Yup from "yup"
import { CartContext } from '../../Context/cartContext';

export default function CheckOut() {
 let params =  useParams()
 let {checkOutPayment} = useContext(CartContext)
 let validationSchema = Yup.object({
  phone: Yup.string().matches(/^01[0125][0-9]{8}$/, "Enter valid phone").required('Phone is reguired'),
    details:Yup.string().required('Details is Required').matches(/^[\w-]{3,}$/,"Enter Valid Details"),
    city:Yup.string().required('City is Required').matches(/^[\w-]{3,}$/,"Enter Valid City")
 })
 let formik = useFormik({
  initialValues:{
    phone:'',
    details:'',
    city:''
  },
  onSubmit:payMent,
  validationSchema
 })
 async function payMent(value){
  let request = await checkOutPayment(params.id,value)
  if (request.data.status == "success"){
    window.open(request.data.session.url , "_self")
  }
  // console.log(request);
 }
  return (
    <>
      <div className='container mt-5'>
        <form onSubmit={formik.handleSubmit}>
        <div className='mb-3'>
            <label htmlFor="details">Details:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="text" name="details" id="details" className='form-control' />
            {formik.errors.details && formik.touched.details ? <div className='alert alert-danger mt-2'>
              {formik.errors.details}
            </div> : ''}
          </div>
          <div className='mb-3'>
            <label htmlFor="phone">Phone:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="tel" name="phone" id="phone" className='form-control' />
            {formik.errors.phone && formik.touched.phone ? <div className='alert alert-danger mt-2'>
              {formik.errors.phone}
            </div> : ''}
          </div>
          <div className='mb-3'>
            <label htmlFor="city">City:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="text" name="city" id="city" className='form-control' />
            {formik.errors.city && formik.touched.city ? <div className='alert alert-danger mt-2'>
              {formik.errors.city}
            </div> : ''}
          </div>
          <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='btn btn-outline-info d-block w-100 mt-5'>Pay Now</button>
        </form>
      </div>
    </>
  )
}
