import axios from 'axios'
import { useFormik } from 'formik'
import React, {  useContext, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
import { UserContext } from '../../Context/Context'
import { CartContext } from '../../Context/cartContext'
import { Helmet } from 'react-helmet'
export default function Login() {
  let {getUserCart,setNumItem} = useContext(CartContext)
  let {setToken} = useContext(UserContext)
  let navigate = useNavigate()
  let [errMessage, setErrMessage] = useState('')
  let [loading, setLoading] = useState(true)
  let validationSchema = Yup.object({
    email: Yup.string().email('Invalid Email').required('Email is Required'),
    password: Yup.string().required('Password is required').matches(/^[A-Z][a-z0-9A-Z!@#$%^&*()_-]{6,16}$/,'Must be start with letter Uppercase ,Must Contain 8 Characters,One LowerCase,One Number and one special character')
  })
  let formik = useFormik({
    initialValues: {
      email: '',
      password: ''
    },
    onSubmit: loginApi,
    validationSchema
  })
  async function loginApi(value) {
    setLoading(false)
    let request = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signin', value).catch(function (error) {
      setErrMessage(error.response.data.message)
      setLoading(true)
    })
    if(request?.data.message == "success"){
      setLoading(true)
      localStorage.setItem('userToken', request.data.token)
      setToken(request.data.token)
      userDataCart()
      navigate('/home')
    }
  }
  async function userDataCart(){
    let request = await getUserCart().catch((error)=>{})
      if(request?.data?.status == "success"){
      setNumItem(request.data.numOfCartItems)
    }
  }
  return (
    <>
    <Helmet>
        <meta charSet="utf-8" />
        <title>Login</title>
      </Helmet>
      <div className='container py-3' >
        <h2 className='mt-5 mb-2'>Login Now......</h2>
        {errMessage != '' ? <div className='alert alert-danger my-3'>{errMessage}</div> : ''}

        <form onSubmit={formik.handleSubmit}>
          <div className='mb-3'>
            <label htmlFor="email">Email:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="email" name="email" id="email" className='form-control' />
            {formik.errors.email && formik.touched.email ? <div className='alert alert-danger mt-2'>
              {formik.errors.email}
            </div> : ''}
          </div>
          <div className='mb-3'>
            <label htmlFor="password">Password:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="password" name="password" id="password" className='form-control' />
            {formik.errors.password && formik.touched.password ? <div className='alert alert-danger mt-2'>
              {formik.errors.password}
            </div> : ''}
          </div>
          <Link className='linkk' to='/forgetPassword'>forget your password ?</Link>
          {loading ? <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='btn bg-main text-white d-block ms-auto'>Login Now</button> : <button type='button' className='btn bg-main text-white d-block ms-auto'>
            <i className='fa-solid fa-spinner fa-spin'></i></button>}


        </form>
      </div>
    </>
  )
}
