import React, { useContext, useEffect, useState } from 'react'
import Spinner from '../Spinner/Spinner'
import { WishContexts } from '../../Context/wishContext'
import { CartContext } from '../../Context/cartContext'
import toast, { Toaster } from 'react-hot-toast'
import { Helmet } from 'react-helmet'

export default function WishList() {
  let { addCart, setNumItem } = useContext(CartContext)
  let [wishData, setWishData] = useState(null)
  let { setNumItemWish, getUserWishList, removeWishListItem } = useContext(WishContexts)
  let [Loading, setLoading] = useState(true)
  useEffect(() => {
    getUserDataWishList()
  }, [])
  async function getUserDataWishList() {
    setLoading(true)
    let request = await getUserWishList().catch((error) => {
      if (error.response.data.statusMsg == 'fail') {
        setWishData(null)
        setLoading(false)
      }
    })
    if (request?.data?.status == "success") {
      setLoading(false)
      setWishData(request.data)
    }
    
    // console.log(request?.data);
  }
  async function addToCart(id) {
    let request = await addCart(id).catch((error) => {
      toast.error("This didn't work.")
    })

    if (request.data.status == "success") {
      setNumItem(request.data.numOfCartItems)
      toast.success(request.data.message, {
        icon: '🛺',
        style: {
          borderRadius: '10px',
          background: '#0aad0a',
          color: '#fff',
        },
        duration: 1000,
      })
    }
  }
  async function removeItemWish(id) {
    let request = await removeWishListItem(id)
    if (request?.data.status == "success") {
      setNumItemWish(request.data.count)
      setWishData(request?.data.data)
    }
  }
  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Wish List</title>
      </Helmet>
      <Toaster
        position="top-right"
        reverseOrder={false}
      />
      {Loading ? <Spinner /> : <>
        {wishData == null ? <div className='container bg-main-light p-5 mt-5'>
          <h1>Your Wish List is empty</h1>
        </div> : <div className='container bg-main-light pb-3 mt-5'>

          <div className="row">
            <div className='d-flex justify-content-between mt-5'>
              <div className='p-5'>
                <h2 className='mb-4 fw-bold'>My Wish List</h2>
              </div>
            </div>
            {wishData?.data?.map((element) => {
              return <>
                <div key={element.id} className="col-md-10">

                  <div className="row">
                    <div className="col-md-2 mb-2">
                      <img src={element.imageCover} className='w-100' alt="" />
                    </div>
                    <div className="col-md-10 d-flex align-items-center">
                      <div>
                        <p className='fw-bold'> {element.category.name} </p>
                        <span>{element.price} EGP</span>
                        <button onClick={() => removeItemWish(element.id)} className='cursor-pointer d-block mt-4 btn btn-outline-danger'><i className="fa-solid fa-trash"></i> Remove</button>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-md-2 ">
                  <div className='mt-5'>
                    <button onClick={() => { addToCart(element.id) }} className='btn btn-outline-success' type='submit'>Add To Cart</button>
                  </div>
                </div>
                <hr></hr>
              </>
            })}

          </div>

        </div>}
      </>}
    </>
  )
}

