import React, { useContext } from 'react'
import { Link, NavLink, useNavigate } from 'react-router-dom'
import logo from '../../assets/images/freshcart-logo.svg'
import { UserContext } from '../../Context/Context'
import { CartContext } from '../../Context/cartContext'
import { WishContexts } from '../../Context/wishContext'

export default function Navbar() {
  let { numItem } = useContext(CartContext)
  let { numItemWish } = useContext(WishContexts)
  let { userToken, setToken } = useContext(UserContext)
  let navigate = useNavigate()
  function logOut() {
    localStorage.removeItem('userToken')
    setToken(null)
    navigate('/login')
  }
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-body-tertiary">
        <div className="container">
          <Link className="navbar-brand" to="home"><img src={logo} alt="" /></Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            {userToken != null ? <ul className="navbar-nav m-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <NavLink className="nav-link" to='home'>Home</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to='cart'>Cart</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to='wish'>Wish List</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to='products'>Products</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to='categories'>Categories</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to='brands'>Brands</NavLink>
              </li>
            </ul> : ""}


            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              {userToken != null ?
                <>
                  <li className='nav-item d-flex align-items-center icon mx-4'>
                    <NavLink to='/cart'>
                      <span className='position-relative'>
                        <i className="fa-solid fa-cart-shopping fs-3 text-muted mt-1 cursor-pointer me-2"></i>
                        <span className='position-absolute bottom-0 mb-2 end-0 translate-middle-y bg-main rounded-2 px-1 text-white'>{numItem}</span>
                      </span>
                    </NavLink>
                  </li>
                  <li className="nav-item icon me-4">
                  <NavLink to='/wish'>
                  <span className='position-relative'>
                    <i  className="fa-solid fa-heart fs-2 text-muted mt-1 cursor-pointer "></i>
                    <span className='position-absolute bottom-0 mb-2 end-0 translate-middle-y  bg-main rounded-2 px-1  text-white'>{numItemWish}</span>
                    </span>
                  </NavLink>
                  </li>
                  <li className="nav-item">
                    <span className="nav-link cursor-pointer" onClick={logOut}>LogOut</span>
                  </li>
                </> : <>

                  <li className="nav-item">
                    <NavLink className="nav-link" to='login'>Login</NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" to=''>Register</NavLink>
                  </li>
                </>}

            </ul>

          </div>
        </div>
      </nav>
    </>
  )
}
