import axios from 'axios'
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { Helmet } from 'react-helmet'
import { useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
export default function ForgetPassword() {
  let navigate = useNavigate()
  let [errMessage, setErrMessage] = useState('')
  let validationSchema = Yup.object({
    email:Yup.string().email('Invalid Email').required('Email is Required')
  })
  async function forgotPassword(values){
    let request = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords`,values).catch((error)=>{
      setErrMessage(error.response.data.message)
    })
    if(request?.data.statusMsg == 'success'){
      document.querySelector('.forgotpassword').classList.add('d-none')
      document.querySelector('.restCode').classList.remove('d-none')
    }
  }
  let formik = useFormik({
    initialValues:{
      email:''
    },
    onSubmit:forgotPassword,
    validationSchema
  })
  let validationSchemaCode = Yup.object({
    resetCode:Yup.string().required('Code is Required').matches(/^[0-9]{5,6}$/,'Enter Valid Code')
  })
  async function resetCode(values){
    let request = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode`,values).catch((error)=>{
      setErrMessage(error.response.data.message)
    })
    if(request.data.status == 'Success'){
      navigate('/restPassword')
    }
    console.log(request);
  }
  let formikCode = useFormik({
    initialValues:{
      resetCode:''
    },
    onSubmit:resetCode,
    validationSchema:validationSchemaCode
  })
  return (
    <>
    <Helmet>
        <meta charSet="utf-8" />
        <title>Forget Password</title>
      </Helmet>
      <div className='forgotpassword container mt-5'>
      {errMessage != '' ? <div className='alert alert-danger my-3'>{errMessage}</div> : ''}
        <h1 className='fw-bold'>please enter your Email to verification code</h1>
        <form onSubmit={formik.handleSubmit}>
          <input type="email" name="email" value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur} className='form-control my-4' placeholder='Email' />
          {formik.errors.email && formik.touched.email ? <div className='alert alert-danger mt-2'>
              {formik.errors.email}
            </div> : ''}
          <button className='btn btn-outline-success' type='submit'>Verify</button>
        </form>
      </div>
      <div className='restCode d-none container mt-5'>
      {errMessage != '' ? <div className='alert alert-danger my-3'>{errMessage}</div> : ''}
        <h1 className='fw-bold'>reset your account password</h1>
        <form onSubmit={formikCode.handleSubmit}>
          <input type="tel" name="resetCode" value={formikCode.values.resetCode} onChange={formikCode.handleChange} onBlur={formikCode.handleBlur} className='form-control my-4' placeholder='Code' />
          {formikCode.errors.resetCode && formikCode.touched.resetCode ? <div className='alert alert-danger mt-2'>
              {formikCode.errors.resetCode}
            </div> : ''}
          <button className='btn btn-outline-success' type='submit'>Verify Code</button>
        </form>
      </div>
    </>
  )
}

































































