import React, { useContext } from 'react'
import { RouterProvider, createBrowserRouter, createHashRouter } from 'react-router-dom'
import Layout from './Component/Layout/Layout'
import Home from './Component/Home/Home'
import Categories from './Component/Categories/Categories'
import Cart from './Component/Cart/Cart'
import Login from './Component/Login/Login'
import NotFound from './Component/NotFound/NotFound'
import Register from './Component/Register/Register'
import Products from './Component/Products/Products'
import { UserContext, UserContextProvider } from './Context/Context'
import { QueryClient, QueryClientProvider } from 'react-query'
import ProductsDetails from './Component/ProductsDetails/ProductsDetails'
import ProtectedRouting from './Component/ProtectedRouting/ProtectedRouting'
import ForgetPassword from './Component/ForgetPassword/ForgetPassword'
import { CartContextProvider } from './Context/cartContext'
import Brands from './Component/Brands/Brands'
import CheckOut from './Component/CheckOut/CheckOut'
import RestPassword from './Component/RestPassword/RestPassword'
import { WishContextProvider } from './Context/wishContext'
import WishList from './Component/WishList/WishList'

export default function App() {
  let queryClient = new QueryClient()
  let routes = createHashRouter([
    {
      path: '', element: <Layout />, children: [
        { path: 'home', element: <ProtectedRouting> <Home /> </ProtectedRouting> },
        { path: 'products', element: <ProtectedRouting><Products /></ProtectedRouting> },
        { path: 'productDetails/:id', element: <ProtectedRouting><ProductsDetails /></ProtectedRouting> },
        { path: 'categories', element: <ProtectedRouting><Categories /></ProtectedRouting> },
        { path: 'cart', element: <ProtectedRouting><Cart /></ProtectedRouting> },
        { path: 'wish', element: <ProtectedRouting><WishList /></ProtectedRouting> },
        { path: 'brands', element: <ProtectedRouting><Brands /></ProtectedRouting> },
        { path: 'checkout/:id', element: <ProtectedRouting><CheckOut /></ProtectedRouting> },
        { index: true, element: <Register /> },
        { path: 'forgetPassword', element: <ForgetPassword /> },
        { path: 'restPassword', element: <RestPassword /> },
        { path: 'login', element: <Login /> },
        { path: '*', element: <NotFound /> },
      ]
    }
  ])

  return (
    <>
      <QueryClientProvider client={queryClient}>

        <WishContextProvider>
        <CartContextProvider>
          <UserContextProvider>

            <RouterProvider router={routes}></RouterProvider>
          </UserContextProvider>
        </CartContextProvider>
        </WishContextProvider>

      </QueryClientProvider>
    </>
  )
}
