import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import Spinner from '../Spinner/Spinner'
import { useQuery } from 'react-query'
import { Link } from 'react-router-dom'
import MainSlider from '../MainSlider/MainSlider'
import { CartContext } from '../../Context/cartContext'
import toast, { Toaster } from 'react-hot-toast';
import CategorySlider from '../CategorySlider/CategorySlider'
import { WishContexts } from '../../Context/wishContext'
import { Helmet } from 'react-helmet'
export default function Home() {
  let { addCart, setNumItem } = useContext(CartContext)
  let { addWishList, setNumItemWish } = useContext(WishContexts)
  let [page, setPage] = useState(1)
  function getPageNum(event) {
    let page = event.target.getAttribute('pageNum')
    setPage(page)
  }
  function getAllProduct(page = 1) {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/products?page=${page.queryKey[1]}`)
  }
  async function addToCart(id) {
    let request = await addCart(id).catch((error) => {
      toast.error("This didn't work.")
    })

    if (request.data.status == "success") {
      setNumItem(request.data.numOfCartItems)
      toast.success(request.data.message, {
        icon: '🛺',
        style: {
          borderRadius: '10px',
          background: '#0aad0a',
          color: '#fff',
        },
        duration: 1000,
      })
    }
  }
  async function addToWishList(e, id) {
    
    let request = await addWishList(id).catch((error) => {
      toast.error("This didn't work.")
    })
    if (request?.data?.status == "success") {
      setNumItemWish(request?.data.count);
      toast.success(request.data.message, {
        icon: '❤',
        style: {
          borderRadius: '10px',
          background: '#fff',
          color: '#61dafb',
        },
        duration: 1000,
      })
    }
    if(request?.data?.data.includes(id)){
      e.target.classList.replace('fa-regular', 'fa-solid')
    }
  }

  let { isLoading, data } = useQuery(["allProducts", page], getAllProduct)
  let [searchList, setSearchList] = useState([])
  function searchInput(e) {
    let term = e.target.value
    let newArr = data?.data.data.filter((el) => el?.category.name.toLowerCase().trim().includes(term.toLowerCase().trim()))
    setSearchList(newArr)
  }

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Home</title>
      </Helmet>
      <Toaster
        position="top-right"
        reverseOrder={false}
      />
      <MainSlider></MainSlider>
      <CategorySlider></CategorySlider>
      {isLoading ? <Spinner /> :

        <div className="container">
          <input onChange={searchInput} type="text" name="search" className='form-control w-75 mx-auto mb-3' placeholder='Search....' />
          <div className="row g-4">
            {
              searchList.length ? searchList?.map((element) => {
                return <div key={element.id} className="col-lg-3 col-md-6 position-relative">
                  <div className='product rounded-3 p-3 cursor-pointer'>
                    <Link to={`/productDetails/${element.id}`}>
                      <img src={element.imageCover} className='w-100' alt="" />
                      <h6 className='text-main'>{element.category.name}</h6>
                      <h5 className='fw-bold'>{element.title.split(" ").slice(0, 2).join(" ")}</h5>
                      <div className='d-flex justify-content-between'>
                        <span>{element.price} EGP</span>
                        <span> <i className='fa-solid fa-star rating-color'></i> {element.ratingsAverage} </span>
                      </div>
                    </Link>
                    <button onClick={() => { addToCart(element.id) }} className='btn bg-main text-white d-block mx-auto'><i className="fa-solid  fa-plus pe-4"></i>Add</button>
                  </div>
                  <i onClick={(e) => { addToWishList(e, element.id) }} className="fa-regular fa-heart fs-3 position-absolute me-4 mt-3 top-0 end-0 cursor-pointer text-info"></i>
                </div>
              }) : data?.data.data.map((element) => {
                return <div key={element.id} className="col-lg-3 col-md-6 position-relative">
                  <div className='product rounded-3 p-3 cursor-pointer'>
                    <Link to={`/productDetails/${element.id}`}>
                      <img src={element.imageCover} className='w-100' alt="" />
                      <h6 className='text-main'>{element.category.name}</h6>
                      <h5 className='fw-bold'>{element.title.split(" ").slice(0, 2).join(" ")}</h5>
                      <div className='d-flex justify-content-between'>
                        <span>{element.price} EGP</span>
                        <span> <i className='fa-solid fa-star rating-color'></i> {element.ratingsAverage} </span>
                      </div>
                    </Link>
                    <button onClick={() => { addToCart(element.id) }} className='btn bg-main text-white d-block mx-auto'><i className="fa-solid  fa-plus pe-4"></i>Add</button>
                  </div>
                  <i onClick={(e) => { addToWishList(e, element.id) }} className="fa-regular fa-heart fs-3 position-absolute me-4 mt-3 top-0 end-0 cursor-pointer text-info"></i>
                </div>
              })
            }


          </div>
          <nav aria-label="Page navigation example">
            <ul className="pagination justify-content-center">
              <li className="page-item">
                <a className="page-link" href="#" aria-label="Previous">
                  <span aria-hidden="true">&laquo;</span>
                </a>
              </li>
              <li className="page-item cursor-pointer"><a className="page-link" pageNum='1' onClick={getPageNum}>1</a></li>
              <li className="page-item cursor-pointer"><a className="page-link" pageNum='2' onClick={getPageNum}>2</a></li>
              <li className="page-item">
                <a className="page-link" href="#" aria-label="Next">
                  <span aria-hidden="true">&raquo;</span>
                </a>
              </li>
            </ul>
          </nav>
        </div>}


    </>
  )
}
