import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react'
import { useQuery } from 'react-query';
import { useParams } from 'react-router-dom'
import Spinner from '../Spinner/Spinner';
import { CartContext } from '../../Context/cartContext';
import toast, { Toaster } from 'react-hot-toast';
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import { Helmet } from 'react-helmet';
import { WishContexts } from '../../Context/wishContext';
export default function ProductsDetails() {
  let {addCart,setNumItem } = useContext(CartContext)
  let { addWishList, setNumItemWish } = useContext(WishContexts)
  let params = useParams()
  let [productId, setProductId] = useState()
  useEffect(()=>{
    setProductId(params.id)
  },[])
  let {data , isLoading} = useQuery(['productDetails',productId],getProductDetails)
  function getProductDetails(query){
    return axios.get(`https://ecommerce.routemisr.com/api/v1/products/${query.queryKey[1]}`)
  }
  async function addToCart(id){
    let request = await addCart(id).catch((error)=>{
      toast.error("This didn't work.")
    })
    if(request.data.status == 'success'){
      setNumItem(request.data.numOfCartItems)
      toast.success(request.data.message,{
        icon: '🛺',
        style: {
          borderRadius: '10px',
          background: '#0aad0a',
          color: '#fff',
        },
        duration: 1000,
      })
    }
  }
  async function addToWishList(e, id) {
    
    let request = await addWishList(id).catch((error) => {
      toast.error("This didn't work.")
    })
    if (request?.data?.status == "success") {
      setNumItemWish(request?.data.count);
      toast.success(request.data.message, {
        icon: '❤',
        style: {
          borderRadius: '10px',
          background: '#fff',
          color: '#61dafb',
        },
        duration: 1000,
      })
    }
    if(request?.data?.data.includes(id)){
      e.target.classList.replace('fa-regular', 'fa-solid')
    }
  }
  return (
    <>
    <Helmet>
        <meta charSet="utf-8" />
        <title>{data?.data.data.title}</title>
      </Helmet>
    <Toaster
        position="top-right"
        reverseOrder={false}
      />
    {isLoading ? <Spinner/> : <div className='container mt-5'>
        <div className="row align-items-center">
          <div className="col-md-4">
            <OwlCarousel items={1} dots loop>
            <img src={data?.data.data.imageCover} className='w-100' alt="" />
            {data?.data?.data.images.map((element)=>{
             return <img src={element} className='w-100' alt="" />
            })}
            </OwlCarousel>
          </div>
          <div className="col-md-8">
            <h4>{data?.data.data.category.name}</h4>
            <p className='text-muted'>{data?.data.data.description}</p>
            <div className='d-flex justify-content-between'>
              <span>{data?.data.data.price} EGP</span>
              <span><i className='fa-solid fa-star rating-color'></i> {data?.data.data.ratingsAverage} </span>
            </div>
            
            <div className='d-flex align-items-center'>
            <button onClick={()=>addToCart(data?.data.data.id)} className='btn bg-main text-white d-block w-50 mx-auto mt-5'>Add To Cart</button>
            <i  onClick={(e) => { addToWishList(e, data?.data.data.id) }} className="fa-regular fa-heart fs-3 mt-5 cursor-pointer text-info "></i>
            </div>
          </div>
        </div>
      </div>}
      
    </>
  )
}
