import React, { useContext, useEffect } from 'react'
import Navbar from '../Navbar/Navbar'
import { Outlet } from 'react-router-dom'
import { UserContext } from '../../Context/Context'
import { CartContext } from '../../Context/cartContext'
import { WishContexts } from '../../Context/wishContext'
import { Offline, Online } from 'react-detect-offline'

export default function Layout() {
  let {getUserCart,setNumItem} = useContext(CartContext)
  let {getUserWishList,setNumItemWish} = useContext(WishContexts)
  let {setToken} = useContext(UserContext)
  useEffect(()=>{

    if(localStorage.getItem('userToken') != null){
      setToken(localStorage.getItem('userToken'))
      userDataCart()
      userDataWishList()
    }
    
  },[])
  async function userDataCart(){
    let request = await getUserCart().catch((error)=>{
    })
 
    if(request?.data?.status == "success"){
      setNumItem(request.data.numOfCartItems)
    }
  }
  async function userDataWishList(){
    let request = await getUserWishList().catch((error)=>{
    })
 
    if(request?.data?.status == "success"){
      setNumItemWish(request.data.count)
    }
  }
  return (
    <>
    <div className='network'>

    <Offline>You're offline right now. Check your connection.</Offline>
    </div>
      <Navbar />
      <Outlet />
    </>
  )
}
