import axios from 'axios'
import React, { useEffect, useState } from 'react'
import Spinner from '../Spinner/Spinner'
import { useQuery } from 'react-query'
import { Link, useParams } from 'react-router-dom'
import { Helmet } from 'react-helmet'


export default function Categories() {
  // let params = useParams()
  let [subcategory,setSubcategory] = useState([])
  let [subcategoryName,setSubcategoryName] = useState()
  function getAllCategory() {
    return axios.get(`https://ecommerce.routemisr.com/api/v1/categories`)
  }
  let { isLoading, data } = useQuery(["allCategory"], getAllCategory)
  async function getSubCategory(e,id,name) {
    let request = await axios.get(`https://ecommerce.routemisr.com/api/v1/categories/${id}/subcategories`).catch((error) => { })
    console.log(request?.data.data)
    setSubcategory(request?.data.data)
    setSubcategoryName(name)
  }
  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Categories</title>
      </Helmet>
      {isLoading ? <Spinner /> :
        <div className="container my-5">
          <div className="row g-4">
            {data?.data?.data.map((element) => {
              return <div key={element._id} onClick={(e) => { getSubCategory(e,element._id,element.name) }} className="col-lg-4 col-md-6 position-relative">
                <div className='category cursor-pointer h-75'>
                  <img src={element.image} className='w-100 mb-4 h-75' alt="" />
                  <h2 className='text-main text-center fw-bold'>{element.name}</h2>
                </div>
              </div>
              
            })}

          </div>
        <div className="row">
              
          {
            subcategoryName ? <h2 className='text-center text-main fw-bold my-3'>{subcategoryName} SubCategories</h2> :''
          }
          {
            subcategory.length? subcategory?.map((element)=>{
              return <> 
              <div key={element._id} className='col-lg-4 col-md-6 ' >
                      <div className='category mt-4 p-4 h-75'>
                        <h4 className='text-center fw-bold'>{element.name}</h4>
                      </div>
                     </div>
              
              </>
            }):""
            // data?.data?.data.map((element)=>{
            //   return <div key={element._id} className='col-lg-4 col-md-6'>
            //           <div className='category mt-4 py-4'>
            //             <h2 className='text-center fw-bold'>{element.name}</h2>
            //           </div>
            //          </div>
            // })
          }
        
        </div>
        </div>}


    </>
  )
}
