import axios from 'axios'
import React, { useEffect, useState } from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

export default function CategorySlider() {
    let [categoryList, setCategoryList] = useState([])
    useEffect(() => {
        getCategory()
    }, [])
    async function getCategory() {
        let request = await axios.get(`https://ecommerce.routemisr.com/api/v1/categories`)
        setCategoryList(request.data.data)
    }
    return (
        <>
            <OwlCarousel items={6} loop >
                {categoryList.map((element) => {
                    return <div className='item my-5'>
                        <img src={element.image} height={250} alt="" />
                        <h5 className='fw-bold'> {element.name} </h5>
                    </div>
                })}
            </OwlCarousel>
        </>
    )
}
