import axios from 'axios'
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { Helmet } from 'react-helmet'
import { useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
export default function Register() {
  let navigate = useNavigate()
  let [errMessage, setErrMessage] = useState('')
  let [loading, setLoading] = useState(true)
  let validationSchema = Yup.object({
    name: Yup.string(null).required('Name is required').min(2, 'Too Short!').max(20, 'Too Long!'),
    email: Yup.string().email('Invalid Email').required('Email is Required'),
    phone: Yup.string().matches(/^01[0125][0-9]{8}$/, "Enter valid phone").required('Phone is reguired'),
    password: Yup.string().required('Password is required').matches(/^[A-Z][a-z0-9A-Z!@#$%^&*()_-]{6,16}$/, 'Must be start with letter Uppercase ,Must Contain 8 Characters,One LowerCase,One Number and one special character'),
    rePassword: Yup.string().required('Password is required').oneOf([Yup.ref('password')], "Passwords don't match")
  })
  let formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      phone: '',
      password: '',
      rePassword: ''
    },
    onSubmit: registerApi,
    validationSchema
  })
  async function registerApi(value) {
    setLoading(false)
    let request = await axios.post('https://ecommerce.routemisr.com/api/v1/auth/signup', value).catch(function (error) {
      setErrMessage(error.response.data.message)
      setLoading(true)
    })
    if (request?.data.message == "success") {
      navigate('/login')
      setLoading(true)
    }
  }
  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Register</title>
      </Helmet>
      <div className='container py-3' >
        <h2 className='mt-5 mb-2'>Register Now......</h2>
        {errMessage != '' ? <div className='alert alert-danger my-3'>{errMessage}</div> : ''}

        <form onSubmit={formik.handleSubmit}>
          <div className='mb-3'>
            <label htmlFor="name">Name:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="text" name="name" id="name" className='form-control' />
            {formik.errors.name && formik.touched.name ? <div className='alert alert-danger mt-2'>
              {formik.errors.name}
            </div> : ''}
          </div>
          <div className='mb-3'>
            <label htmlFor="email">Email:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="email" name="email" id="email" className='form-control' />
            {formik.errors.email && formik.touched.email ? <div className='alert alert-danger mt-2'>
              {formik.errors.email}
            </div> : ''}
          </div>
          <div className='mb-3'>
            <label htmlFor="password">Password:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="password" name="password" id="password" className='form-control' />
            {formik.errors.password && formik.touched.password ? <div className='alert alert-danger mt-2'>
              {formik.errors.password}
            </div> : ''}
          </div>
          <div className='mb-3'>
            <label htmlFor="rePassword">Re-Password:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="password" name="rePassword" id="rePassword" className='form-control' />
            {formik.errors.rePassword && formik.touched.rePassword ? <div className='alert alert-danger mt-2'>
              {formik.errors.rePassword}
            </div> : ''}
          </div>
          <div className='mb-3'>
            <label htmlFor="phone">Phone:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="tel" name="phone" id="phone" className='form-control' />
            {formik.errors.phone && formik.touched.phone ? <div className='alert alert-danger mt-2'>
              {formik.errors.phone}
            </div> : ''}
          </div>
          {loading ? <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='btn bg-main text-white d-block ms-auto'>Register Now</button> : <button type='button' className='btn bg-main text-white d-block ms-auto'>
            <i className='fa-solid fa-spinner fa-spin'></i></button>}


        </form>
      </div>
    </>
  )
}
