import axios from "axios";
import { createContext, useState } from "react";

export let WishContexts = createContext()
export function WishContextProvider({children}){
    let [numItemWish,setNumItemWish] = useState(0)
     function addWishList(productId){
        let option = {
            headers:{
                token:localStorage.getItem('userToken')
            }
        }
        let body ={
            productId
        }
       return  axios.post(`https://ecommerce.routemisr.com/api/v1/wishlist`,body,option)
    }
    function removeWishListItem(id){
        let option = {
            headers:{
                token:localStorage.getItem('userToken')
            }
        }
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/wishlist/${id}`,option)
    }
    function getUserWishList(){
        let option = {
            headers:{
                token:localStorage.getItem('userToken')
            }
        }
        return axios.get('https://ecommerce.routemisr.com/api/v1/wishlist',option)

    }
    return <WishContexts.Provider value={{addWishList,setNumItemWish,numItemWish,getUserWishList,removeWishListItem}}>
        {children}
    </WishContexts.Provider>
}