import axios from "axios";
import { createContext, useState } from "react";

export let CartContext = createContext()
export function CartContextProvider({children}){
    let [numItem,setNumItem] = useState(0)
    function getUserCart(){
        let option = {
            headers:{
                token:localStorage.getItem('userToken')
            }
        }
        return axios.get('https://ecommerce.routemisr.com/api/v1/cart',option)

    }
     function addCart(productId){
        let option = {
            headers:{
                token:localStorage.getItem('userToken')
            }
        }
        let body ={
            productId
        }
        return axios.post('https://ecommerce.routemisr.com/api/v1/cart',body,option)
    }
    function updateCart(id,count){
        let option = {
            headers:{
                token:localStorage.getItem('userToken')
            }
        }
        let body ={
            count
        }
        return  axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,body,option)
    }
    function removeCartItem(id){
        let option = {
            headers:{
                token:localStorage.getItem('userToken')
            }
        }
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,option)

    }
    function clearCart(){
        let option = {
            headers:{
                token:localStorage.getItem('userToken')
            }
        }
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`,option)

    }
    function checkOutPayment(id,shippingAddress){
        let option = {
            headers:{
                token:localStorage.getItem('userToken')
            }
        }
        let body ={
            shippingAddress
        }
        return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${id}?url=http://localhost:3000`,body,option)
    }
    return <CartContext.Provider value={{addCart,numItem,setNumItem,getUserCart,removeCartItem,clearCart,updateCart,checkOutPayment}}>
        {children}
    </CartContext.Provider>
}