import axios from 'axios'
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
export default function RestPassword() {
  let navigate = useNavigate()
  let [errMessage, setErrMessage] = useState('')
  let [loading, setLoading] = useState(true)
  let validationSchema = Yup.object({
    email: Yup.string().email('Invalid Email').required('Email is Required'),
    newPassword: Yup.string().required('newPassword is required').matches(/^[A-Z][a-z0-9A-Z!@#$%^&*()_-]{6,16}$/,'Must be start with letter Uppercase ,Must Contain 8 Characters,One LowerCase,One Number and one special character')
  
  })
  let formik = useFormik({
    initialValues:{
      email:"",
      newPassword: ""
  },
  onSubmit:newPasswordApi,
  validationSchema
  })
  async function newPasswordApi(value){
    let request = await axios.put(`https://ecommerce.routemisr.com/api/v1/auth/resetPassword`,value).catch((error)=>{
      setErrMessage(error.response.data.message)
    })
    if(request.data.token){
      navigate('/login')
    }
  }
  return (
    <>
      <div className='container py-3' >
        {errMessage != '' ? <div className='alert alert-danger my-3'>{errMessage}</div> : ''}
        <h2 className='mt-5 mb-2 fw-bold'>reset your account password</h2>

        <form onSubmit={formik.handleSubmit}>
          <div className='mb-3'>
            <label htmlFor="email">Email:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="email" name="email" id="email" className='form-control' />
            {formik.errors.email && formik.touched.email ? <div className='alert alert-danger mt-2'>
              {formik.errors.email}
            </div> : ''}
          </div>
          <div className='mb-3'>
            <label htmlFor="newPassword">newPassword:</label>
            <input onChange={formik.handleChange} onBlur={formik.handleBlur} type="password" name="newPassword" id="newPassword" className='form-control' />
            {formik.errors.newPassword && formik.touched.newPassword ? <div className='alert alert-danger mt-2'>
              {formik.errors.newPassword}
            </div> : ''}
          </div>
          {loading ? <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='btn btn-outline-success'>Reset Password</button> : <button type='button' className='btn btn-outline-success'>
            <i className='fa-solid fa-spinner fa-spin'></i></button>}


        </form>
      </div>
    </>
  )
}
